"use client";

import { Puck, type Config } from "@measured/puck";
import "@measured/puck/puck.css";
import type { PageData, Section } from "../../lib/schemas";

type PuckData = { components: Record<string, unknown>; content: Section[] };

// Simple site renderers for demo. In a full build, import components/site/*
function HeroRender(props: any) {
  const { heading, subheading, ctaLabel, ctaHref, bgType, bgSrc, overlayOpacity } = props;
  return (
    <section className="relative grid min-h-[40vh] place-items-center overflow-hidden">
      {bgSrc && (
        bgType === "video" ? (
          <video className="absolute inset-0 h-full w-full object-cover" src={bgSrc} autoPlay loop muted />
        ) : (
          <img className="absolute inset-0 h-full w-full object-cover" src={bgSrc} alt="Hero background" />
        )
      )}
      <div className="absolute inset-0" style={{ background: `rgba(0,0,0,${overlayOpacity ?? 0.35})` }} />
      <div className="relative z-10 mx-auto max-w-2xl text-center">
        <h1 className="text-3xl font-bold text-white md:text-5xl">{heading}</h1>
        {subheading && <p className="mt-3 text-white/90">{subheading}</p>}
        {ctaLabel && (
          <a href={ctaHref} className="mt-6 inline-block rounded bg-white/90 px-4 py-2 text-sm font-medium text-black">
            {ctaLabel}
          </a>
        )}
      </div>
    </section>
  );
}

function FAQRender(props: any) {
  const { items } = props as { items: { q: string; a: string }[] };
  return (
    <section className="mx-auto max-w-3xl px-4 py-8">
      <h2 className="mb-4 text-2xl font-semibold">FAQs</h2>
      <div className="space-y-4">
        {items?.map((it, idx) => (
          <div key={idx} className="rounded-md border p-4">
            <p className="font-medium">{it.q}</p>
            <p className="text-sm text-gray-600 dark:text-gray-300">{it.a}</p>
          </div>
        ))}
      </div>
    </section>
  );
}

const config: Config<any> = {
  components: {
    Hero: {
      fields: {
        heading: { type: "text" },
        subheading: { type: "text" },
        ctaLabel: { type: "text" },
        ctaHref: { type: "text" },
        bgType: { type: "select", options: ["image", "video"] },
        bgSrc: { type: "text" },
        overlayOpacity: { type: "number" },
      },
      render: HeroRender,
    },
    FAQ: {
      fields: {
        items: {
          type: "array",
          arrayFields: {
            q: { type: "text" },
            a: { type: "textarea" },
          },
        },
      },
      render: FAQRender,
    },
  },
};

export function PuckProvider({ data, onPublish }: { data: PageData; onPublish?: (data: any) => void }) {
  const puckData: PuckData = {
    components: {},
    content: data.sections as Section[],
  };

  return (
    <div className="min-h-screen">
      <Puck config={config} data={puckData} onPublish={onPublish} />
    </div>
  );
}